<template>
  <div id="places">
    <div class="heading">
      <h2>Places</h2>
      <router-link class="back" :to="{ name: 'Zones', params: { id: $route.params.id } }"></router-link>
    </div>
    <ul>
      <li>
        <span>ID</span>
        <span>Status</span>
        <span>Zone</span>
      </li>
      <li :key="index" v-for="(place, index) in places">
        <a @click.prevent="updateStatus(place)" href="#">
          <span>{{place.id}}</span>
          <span>{{place.status}}</span>
          <span>{{id}}</span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'Places',
  props: [
    "parking"
  ],
  computed: {
    places() {
      return this.parking[this.$route.params.id].zone[this.$route.params.index].places
    },
    id() {
      return this.parking[this.$route.params.id].zone[this.$route.params.index].id
    }
  },
  methods: {
    updateStatus(item) {
      item.status = !item.status
      localStorage.setItem('Parking', JSON.stringify(this.parking));
    }
  }
}
</script>

