<template>
  <div id="zones">
    <div class="heading">
      <h2>Zones</h2>
      <router-link class="back" to="/"></router-link>
    </div>
      <ul>
        <li class="labels">
          <span>ID</span>
          <span>Description</span>
        </li>
        <li class="item" :key="index" v-for="(zone, index) in currentShop">
          <div class="content">
            <span>{{zone.id}}</span>
            <div class="edit">
              <input class="edit__field" v-focus v-if="zone.edit" @blur= "editName(zone);$emit('update')" @keyup.enter = "editName(zone); $emit('update')" type="text" v-model="zone.name">

              <span v-if="!zone.edit" class="edit__toggle" @click="zone.edit = !zone.edit">{{zone.name}}</span>
            </div>
          </div>
          <router-link :to="{ name: 'Places', params: { id: shopID, index: index } }">
            Go
          </router-link>
        </li>
      </ul>
     
  </div>
</template>

<script>

export default {
  name: 'Zones',
  props: [
    "parking"
  ],
  data() {
    return {
    }
  },
  directives: {
    focus: {
      inserted (el) {
        el.focus()
      }
    }
  },
  computed: {
    currentShop() {
      return this.parking[this.$route.params.id].zone
    },
    shopID() {
      return this.$route.params.id
    }
  },
  methods: {
    editName(item) {
      if (item.name === '') {
        item.name = "Zone name";
      }
      item.edit = false;
      localStorage.setItem('Parking', JSON.stringify(this.parking));
    },
  },
}
</script>

