import Vue from 'vue'
import Router from 'vue-router'
import Shops from '@/components/Shops'
import Zones from '@/components/Zones'
import Places from '@/components/Places'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Shops',
      component: Shops
    },
    {
      path: '/zones/:id',
      name: 'Zones',
      component: Zones,
    },
    
    {
      path: '/zones/:id/places/:index',
      name: 'Places',
      component: Places
    },
  ]
})
