<template>
  <div id="shops">
    <div class="heading">
      <h2>Shops</h2>
    </div>
    <ul>
      <li class="labels">
        <span>ID</span>
        <span>Description</span>
      </li>
      <li class="item" :key="index" v-for="(shop, index) in parking">
          <div class="content">
            <span>{{index + 1}}</span>
            <div class="edit">
              <input class="edit__field" v-focus v-if="shop.edit" @blur= "editName(shop);$emit('update')" @keyup.enter = "editName(shop); $emit('update')" type="text" v-model="shop.name">
              <span v-if="!shop.edit" class="edit__toggle" @click="shop.edit = !shop.edit">{{shop.name}}</span>
            </div>
          </div>
          <router-link :to="{ name: 'Zones', params: { id: index } }">
            Go
          </router-link>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'Shops',
  props: [
    "parking"
  ],
  methods: {
    editName(item) {
      if (item.name === '') 
      item.name = "Shop name";
      
      item.edit = false;
      localStorage.setItem('Parking', JSON.stringify(this.parking));
    },
  },
  directives: {
    focus: {
      inserted (el) {
        el.focus()
      }
    }
  },
}
</script>

