<template>
  <div id="app">
    <!-- <img class="logo" alt="logo" src="./assets/skoda.svg"> -->
    <div class="wrap">
       <transition name="slide-fade" mode="out-in">
      <router-view :parking="parking"></router-view>
      </transition>
    </div>
