</template>

<script>

import parking from './db';

export default {
  name: 'app',
  data() {
    return {
      parking: []
    }
  },
  created() {
    let ls = localStorage.getItem("Parking");
    
    if (ls !== null) {
      this.parking = JSON.parse(ls)
    }
    else {
      this.parking = parking;
    }

  }
}
</script>

